// GET - List dispositions
export async function GET(request: NextRequest) {
  try {
    const { searchParams } = new URL(request.url)
    const subAccountId = searchParams.get('subAccountId')
    const dispositionId = searchParams.get('id')
    const category = searchParams.get('category') as DispositionCategory | null
    const activeOnly = searchParams.get('activeOnly') !== 'false'
    
    if (!subAccountId) {
      return NextResponse.json({
        success: false,
        error: 'subAccountId is required'
      }, { status: 400 })
    }
    
    // Single disposition fetch
    if (dispositionId) {
      const { data: disposition, error } = await supabase
        .from('DialerDisposition')
        .select('*')
        .eq('id', dispositionId)
        .eq('subAccountId', subAccountId)
        .single()
      
      if (error) {
        return NextResponse.json({
          success: false,
          error: error.message
        }, { status: 404 })
      }
      
      return NextResponse.json({
        success: true,
        data: disposition
      })
    }
    
    // Check if dispositions exist, create defaults if not
    const { count } = await supabase
      .from('DialerDisposition')
      .select('*', { count: 'exact', head: true })
      .eq('subAccountId', subAccountId)
    
    if (count === 0) {
      // Create default dispositions
      const defaultItems = DEFAULT_DISPOSITIONS.map(d => ({
        ...d,
        subAccountId,
        isSystem: true,
        isActive: true,
        requiresNotes: false,
        requiresCallback: d.requiresCallback || false,
        createsDeal: false,
        timesUsed: 0
      }))
      
      await supabase
        .from('DialerDisposition')
        .insert(defaultItems)
    }
    
    // List dispositions
    let query = supabase
      .from('DialerDisposition')
      .select('*')
      .eq('subAccountId', subAccountId)
      .order('displayOrder', { ascending: true })
    
    if (activeOnly) {
      query = query.eq('isActive', true)
    }
    
    if (category) {
      query = query.eq('category', category)
    }
    
    const { data: dispositions, error } = await query
    
    if (error) {
      return NextResponse.json({
        success: false,
        error: error.message
      }, { status: 500 })
    }
    
    return NextResponse.json({
      success: true,
      data: dispositions || []
    })
    
  } catch (error) {
    console.error('GET /api/v1/dialer/dispositions error:', error)
    return NextResponse.json({
      success: false,
      error: 'Internal server error'
    }, { status: 500 })
  }
}

// POST - Create a new disposition
export async function POST(request: NextRequest) {
  try {
    const body = await request.json()
    const { subAccountId, ...dispositionData } = body
    
    if (!subAccountId) {
      return NextResponse.json({
        success: false,
        error: 'subAccountId is required'
      }, { status: 400 })
    }
    
    if (!dispositionData.name || !dispositionData.code) {
      return NextResponse.json({
        success: false,
        error: 'name and code are required'
      }, { status: 400 })
    }
    
    // Check for duplicate code
    const { data: existing } = await supabase
      .from('DialerDisposition')
      .select('id')
      .eq('subAccountId', subAccountId)
      .eq('code', dispositionData.code)
      .single()
    
    if (existing) {
      return NextResponse.json({
        success: false,
        error: 'Disposition code already exists'
      }, { status: 400 })
    }
    
    // Get max display order
    const { data: maxOrder } = await supabase
      .from('DialerDisposition')
      .select('displayOrder')
      .eq('subAccountId', subAccountId)
      .order('displayOrder', { ascending: false })
      .limit(1)
      .single()
    
    // Create disposition
    const { data: disposition, error } = await supabase
      .from('DialerDisposition')
      .insert({
        subAccountId,
        name: dispositionData.name,
        code: dispositionData.code.toUpperCase(),
        description: dispositionData.description || null,
        category: dispositionData.category || 'neutral',
        color: dispositionData.color || '#6B7280',
        icon: dispositionData.icon || 'ðŸ“‹',
        requiresNotes: dispositionData.requiresNotes || false,
        requiresCallback: dispositionData.requiresCallback || false,
        removesFromQueue: dispositionData.removesFromQueue !== false,
        updatesContactStatus: dispositionData.updatesContactStatus || null,
        triggersAutomation: dispositionData.triggersAutomation || null,
        movesDealToStage: dispositionData.movesDealToStage || null,
        createsDeal: dispositionData.createsDeal || false,
        dealPipelineId: dispositionData.dealPipelineId || null,
        dealStageId: dispositionData.dealStageId || null,
        displayOrder: (maxOrder?.displayOrder || 0) + 1,
        isActive: true,
        isSystem: false
      })
      .select()
      .single()
    
    if (error) {
      return NextResponse.json({
        success: false,
        error: error.message
      }, { status: 500 })
    }
    
    return NextResponse.json({
      success: true,
      data: disposition,
      message: 'Disposition created successfully'
    }, { status: 201 })
    
  } catch (error) {
    console.error('POST /api/v1/dialer/dispositions error:', error)
    return NextResponse.json({
      success: false,
      error: 'Internal server error'
    }, { status: 500 })
  }
}

// PUT - Update a disposition
export async function PUT(request: NextRequest) {
  try {
    const body = await request.json()
    const { id, subAccountId, ...initialUpdateData } = body
    
    if (!id || !subAccountId) {
      return NextResponse.json({
        success: false,
        error: 'id and subAccountId are required'
      }, { status: 400 })
    }
    
    // Check if it's a system disposition
    const { data: existing } = await supabase
      .from('DialerDisposition')
      .select('isSystem')
      .eq('id', id)
      .single()
    
    let updateData = initialUpdateData
    
    if (existing?.isSystem) {
      // System dispositions can only have limited fields updated
      const allowedUpdates = ['isActive', 'displayOrder', 'color', 'icon']
      const filteredUpdates: Record<string, unknown> = {}
      for (const key of allowedUpdates) {
        if (initialUpdateData[key] !== undefined) {
          filteredUpdates[key] = initialUpdateData[key]
        }
      }
      updateData = filteredUpdates
    }
    
    // Update disposition
    const { data: disposition, error } = await supabase
      .from('DialerDisposition')
      .update({
        ...updateData,
        updatedAt: new Date().toISOString()
      })
      .eq('id', id)
      .eq('subAccountId', subAccountId)
      .select()
      .single()
    
    if (error) {
      return NextResponse.json({
        success: false,
        error: error.message
      }, { status: 500 })
    }
    
    return NextResponse.json({
      success: true,
      data: disposition,
      message: 'Disposition updated successfully'
    })
    
  } catch (error) {
    console.error('PUT /api/v1/dialer/dispositions error:', error)
    return NextResponse.json({
      success: false,
      error: 'Internal server error'
    }, { status: 500 })
  }
}

// DELETE - Delete a disposition (only non-system)
export async function DELETE(request: NextRequest) {
  try {
    const { searchParams } = new URL(request.url)
    const id = searchParams.get('id')
    const subAccountId = searchParams.get('subAccountId')
    
    if (!id || !subAccountId) {
      return NextResponse.json({
        success: false,
        error: 'id and subAccountId are required'
      }, { status: 400 })
    }
    
    // Check if it's a system disposition
    const { data: existing } = await supabase
      .from('DialerDisposition')
      .select('isSystem')
      .eq('id', id)
      .single()
    
    if (existing?.isSystem) {
      return NextResponse.json({
        success: false,
        error: 'Cannot delete system dispositions'
      }, { status: 400 })
    }
    
    // Delete disposition
    const { error } = await supabase
      .from('DialerDisposition')
      .delete()
      .eq('id', id)
      .eq('subAccountId', subAccountId)
    
    if (error) {
      return NextResponse.json({
        success: false,
        error: error.message
      }, { status: 500 })
    }
    
    return NextResponse.json({
      success: true,
      message: 'Disposition deleted successfully'
    })
    
  } catch (error) {
    console.error('DELETE /api/v1/dialer/dispositions error:', error)
    return NextResponse.json({
      success: false,
      error: 'Internal server error'
    }, { status: 500 })
  }
}
